
main, section, footer{
    overflow: hidden;
}


ul, ol{
    padding: 0;
    margin: 0;
    list-style: none;
}

h1, h2, h3, h4, h5, h6, p, .h1, .h2, .h3, .h4, .h5, .h6, .h7, .h8{
    margin-bottom: 0;
    line-height: 1.5rem;
}

a{
    text-decoration: none;
    vertical-align: middle;

}

img{
    max-width: 100%;
    object-fit: cover;
}

i{
    vertical-align: middle;
}

